import * as THREE from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js'
import GUI from 'lil-gui'
export default function SingleModelLoader(
  canvas,
  filename,
  bg,
  rotateAt,
  defaultRotation,
  defaultViewAngle,
) {
  const scene = new THREE.Scene()
  const camera = new THREE.PerspectiveCamera(
    60,
    window.innerWidth / window.innerHeight,
    0.1,
    100,
  )
  const renderer = new THREE.WebGL1Renderer({
    canvas: canvas,
  })
  const controls = new OrbitControls(camera, renderer.domElement)

  renderer.setPixelRatio(window.devicePixelRatio)
  renderer.setSize(window.innerWidth)

  camera.position.set(0, 0.5, 1)

  // let size = 'bigger'
  // if(size==="biggest"){

  // }else if (size === 'bigger') {
  // }else if(size === "medium"){

  // }else if(size==="small"){

  // }

  scene.background = new THREE.Color(`${bg}`)
  console.log('backgorund: ', bg)
  scene.translateY(0.05)

  renderer.render(scene, camera)

  const loader = new GLTFLoader()
  const light = new THREE.AmbientLight(0xffffff, 1)

  loader.load(
    `./models/${filename}`,
    // `./models/scene.glb`,
    function (glb) {
      const box = new THREE.Box3().setFromObject(glb.scene)
      var size = new THREE.Vector3()
      box.getSize(size)
      var center = new THREE.Vector3()
      box.getCenter(center)
      var scale = window.innerHeight / window.innerWidth / size.y
      glb.scene.scale.setScalar(scale)

      glb.scene.position.sub(center.multiplyScalar(scale))

      // glb.scene.position.x = 0
      // glb.scene.position.y = 0
      // glb.scene.position.z = 0

      // const axesHelper = new THREE.AxesHelper(5)
      // glb.scene.add(axesHelper)
      //   glb.scene.scale.set(0.015, 0.015, 0.013)

      console.log('defaultRotation and rotateAt: ', defaultRotation, rotateAt)

      if (defaultRotation === true) {
        rotateAt = null
      } else {
        defaultRotation = false
      }

      // if (defaultViewAngle === 'front') {
      //   // glb.scene.rotateX(90)
      //   // glb.scene.rotateY(0)
      //   // glb.scene.rotateZ(90)
      // } else if (defaultViewAngle === 'back') {
      //   glb.scene.rotateY(90)
      // } else if (defaultViewAngle === 'top') {
      //   glb.scene.rotateX(180)
      //   glb.scene.rotateY(90)
      //   glb.scene.rotateZ(90)
      // } else if (defaultViewAngle === 'bottom') {
      //   glb.scene.rotateX(90)
      //   glb.scene.rotateY(0)
      //   glb.scene.rotateZ(90)
      // } else if (defaultViewAngle === 'left') {
      //   glb.scene.rotateX(90)
      //   glb.scene.rotateY(0)
      //   glb.scene.rotateZ(90)
      // } else if (defaultViewAngle === 'right') {
      //   glb.scene.rotateX(90)
      //   glb.scene.rotateY(0)
      //   glb.scene.rotateZ(90)
      // }

      if (rotateAt === 'front') {
        // glb.scene.rotateX(90)
        // glb.scene.rotateY(0)
        // glb.scene.rotateZ(90)
      } else if (rotateAt === 'back') {
        glb.scene.rotateY(THREE.MathUtils.radToDeg(80)) //500
      } else if (rotateAt === 'top') {
        camera.position.set(0, 1, 0.5)
      } else if (rotateAt === 'bottom') {
        glb.scene.rotateZ(THREE.MathUtils.radToDeg(-45))
        glb.scene.rotateX(THREE.MathUtils.radToDeg(40))
      } else if (rotateAt === 'left') {
        glb.scene.rotateY(THREE.MathUtils.radToDeg(40)) //
      } else if (rotateAt === 'right') {
        glb.scene.rotateY(THREE.MathUtils.radToDeg(-40)) //
      }

      function animate() {
        requestAnimationFrame(animate)
        if ((defaultRotation === true) & (rotateAt === null)) {
          glb.scene.rotation.y += 0.01
        }
        renderer.render(scene, camera)
      }
      animate()
      class ColorGUIHelper {
        constructor(object, prop) {
          this.object = object
          this.prop = prop
        }
        get value() {
          return `#${this.object[this.prop].getHexString()}`
        }
        set value(hexString) {
          this.object[this.prop].set(hexString)
        }
      }

      {
        const color = 0xffffff
        const intensity = 1
        const light = new THREE.AmbientLight(color, intensity)
        scene.add(light)

        const gui = new GUI()
        gui.addColor(new ColorGUIHelper(light, 'color'), 'value').name('color')
        gui.add(light, 'intensity', 0, 2, 0.01)
      }

      scene.add(glb.scene)
    },
    function (xhr) {
      let loaded = (xhr.loaded / xhr.total) * 100
      let element = document.createElement('div')
      let textNode = document.createTextNode(loaded)
      element.classList.add('loading-model')
      element.appendChild(textNode)
      //   console.log('loaded, total: percentage:  ', xhr.loaded, xhr.total)
    },
    function (error) {
      console.log('glb load error: ', error)
    },
  )

  console.log('loader: ', loader)

  const radius = 10
  const radials = 16
  const circles = 8
  const divisions = 64

  const helper = new THREE.PolarGridHelper(radius, radials, circles, divisions)
  //   scene.add(helper)

  scene.add(light)

  function addStars() {
    const starGeometry = new THREE.SphereGeometry(0.05)
    const starMaterial = new THREE.MeshBasicMaterial({ color: 0xffffff })
    const star = new THREE.Mesh(starGeometry, starMaterial)

    const [x, y, z] = Array(3)
      .fill()
      .map(() => THREE.MathUtils.randFloatSpread(100))
    star.position.set(x, y, z)

    scene.add(star)
  }

  for (let i = 0; i < 500; i++) {
    addStars()
  }

  window.addEventListener('resize', function (e) {
    console.log(
      'canvas width, widnow width: ',
      renderer.domElement.width,
      window.innerWidth,
    )
    if (renderer.domElement.width !== window.innerWidth) {
      renderer.setSize(window.innerWidth)
    }
  })

  function resizeRendererToDisplaySize(renderer) {
    const canvas = renderer.domElement
    const width = canvas.clientWidth
    const height = canvas.clientHeight
    const needResize = canvas.width !== width || canvas.height !== height
    if (needResize) {
      renderer.setSize(width, height, false)
    }
    return needResize
  }

  function animate() {
    requestAnimationFrame(animate)
    if (resizeRendererToDisplaySize(renderer)) {
      const canvas = renderer.domElement
      camera.aspect = canvas.clientWidth / canvas.clientHeight
      camera.updateProjectionMatrix()
    }
    controls.update()
    renderer.render(scene, camera)
  }
  animate()
}
